import React from 'react';
import { NavLink } from 'react-router-dom';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css'


const Header =() => {
    return(
        <>
            <div className="contaier-fluid">
                <nav className="navbar navbar-expand-lg navbar-dark bg-dark row">
                    <NavLink className="navbar-brand display-5 p-4 col-6" to="/">Neeraj Yadav</NavLink>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    
                        <div className="collapse navbar-collapse justify-content-center col-6 " id="navbarSupportedContent">
                            <ul className="navbar-nav mr-auto">
                                <li className="nav-item mx-3 active">
                                    <NavLink className="nav-link" to="/">Home</NavLink>
                                </li>
                                <li className="nav-item mx-3">
                                    <NavLink className="nav-link" to="/education">Education</NavLink>
                                </li>
                                <li className="nav-item mx-3">
                                    <NavLink className="nav-link" to="/skill">Skills</NavLink>
                                </li>
                                <li className="nav-item mx-3">
                                    <NavLink className="nav-link" to="/about">About Me</NavLink>
                                </li>
                                <li className="nav-item mx-3">
                                    <NavLink className="nav-link" to="/contact">Contact</NavLink>
                                </li>
                            </ul>
                        </div>
                    
                </nav>
            </div>
        </>
    )
}

export default Header;