import React from 'react';
import Header from './Header';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';

const Skill = () => {
    return(
        <>
        <Header/>
        <div className="container-fluid bg-dark text-light">
            <div className="container">
                <div className="row">
                    <div className="col-12">
                        <h1 className=" display-2">Skills</h1>
                        <hr/>
                    </div>
                </div>
                <div className="row">
                    <div className="col-12 text-success">
                        <h2 className="h2 m-1 p-2">HTML</h2>
                        <div class="progress">
                            <div class="progress-bar" role="progressbar" style={{width: "80%"}} aria-valuenow="80" aria-valuemin="0" aria-valuemax="100"></div>
                        </div>
                        <h2 className="h2 m-1 p-2">CSS</h2>
                        <div class="progress">
                            <div class="progress-bar" role="progressbar" style={{width: "85%"}} aria-valuenow="85" aria-valuemin="0" aria-valuemax="100"></div>
                        </div>
                        <h2 className="h2 m-1 p-2">JavaScript</h2>
                        <div class="progress">
                            <div class="progress-bar" role="progressbar" style={{width: "70%"}} aria-valuenow="70" aria-valuemin="0" aria-valuemax="100"></div>
                        </div>
                        <h2 className="h2 m-1 p-2">ReactJS</h2>
                        <div class="progress">
                            <div class="progress-bar" role="progressbar" style={{width: "65%"}} aria-valuenow="65" aria-valuemin="0" aria-valuemax="100"></div>
                        </div>
                        <h2 className="h2 m-1 p-2">Photoshop</h2>
                        <div class="progress">
                            <div class="progress-bar" role="progressbar" style={{width: "60%"}} aria-valuenow="60" aria-valuemin="0" aria-valuemax="100"></div>
                        </div>
                        <h2 className="h2 m-1 p-2">Bootstrap</h2>
                        <div class="progress">
                            <div class="progress-bar" role="progressbar" style={{width: "90%"}} aria-valuenow="90" aria-valuemin="0" aria-valuemax="100"></div>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-12 text-secondary">
                        <h2 className="display-3 p-4">practicing more on all of the above..... </h2>
                    </div>
                </div>
            </div>
        </div>
        </>
    )
}

export default Skill;